/* General section styles */
.section {
    position: relative;
    width: 100%;
    height: auto;
    background: #ffffff;
    top: -15px;
    padding-bottom: 200px;
}

.sectionContent {
    padding-bottom: 50px;
    min-width: 700px;
    max-width: 60%;
    margin: auto;
}

.sectionTitle {
    width: 800px;
    margin: auto;
    text-align: center;
    font-family: typograph-extrabold;
    font-size: 40px;
    color: #00083c

}

.sectionSubtitle {
    width: 100%;
    font-size: 24px; 
    font-family: typograph-semibold-italic;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
    color: #53587a
}

/* About Me section styles */
.aboutMeImageOutline {
    background: linear-gradient(#bac0ff, #fb8686);
    width: 340px;
    height: 340px;
    border-radius: 1000px;
    margin: 40px auto;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center; 

    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    z-index: 2;
    position: relative;
    
}

.aboutMeImage {

    width: 300px;
    height: 300px;
    margin: auto;
    border-radius: 1000px;
    background-image: url("../static/profile.jpeg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    
}

.aboutMeTag {
    width: 260px;
    height: 100px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    top: -130px;
    left: -120px;
    position: relative;
    margin: auto;
    box-sizing: border-box;
    padding: 15px;
    z-index: 3;
}

.nameTag {
    font-family: quando-bold;
    font-weight: 900;
    font-size: 20px;
    padding-top: 5px;
    width: 100%;
}
.ageTag {
    font-family: typograph-light;
    font-weight: 200;
    font-size: 16px;
    padding-top: 5px;
    width: 100%;
}
.locationTag {
    font-family: typograph-light;
    font-size: 14px;
    width: 100%;
    text-align: right;
}

.aboutMeDescription {
    background-color: white;
    width: 100%;
    border-radius: 20px;
    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);
    margin: auto;
    height: auto;
    box-sizing: border-box;
    padding: 20px;
    margin-bottom: 10px;

    font-size: 20px;
    font-family: typograph-light;
    position: relative;
    top: -100px;
}


.skillContainer {
    width: 100px;
    height: 100px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) translateY(150%);
}

.skill-large {
    background-color: white;
    height: 86px;
    border-radius: 1000px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    min-width: 86px;

    transform: translateX(0px) translateY(0px);
    opacity: 0;

    box-shadow: 3px 3px 20px 10px rgba(0, 0, 0, 0.1);

    transition: justify-content 0.1s, transform 0.3s, opacity 0.1s;
    transition-delay: 0.2s;
    z-index: -1;
    
}

.icon-skill-large {

    width: 64px;
    height: 64px;
    padding: 0px 20px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;

}

.text-skill-large {
    padding-right: 0px;
    font-size: 16px;
    font-family: typograph-semibold;
    width: 0px;
    opacity: 0;

    transition: width 0.1s, padding 0.1s, opacity 0.1s;
}

.text-skill-large-animated {
    padding-right: 20px;
    font-size: 16px;
    font-family: typograph-semibold;
    width: auto;
    opacity: 1;


    transition: width 0.3s, padding 0.3s, opacity 0.1s;
}

.skill-large-animated {
    justify-content: space-evenly;
    opacity: 1;
    transform: translateX(0px) translateY(0px);
    transition: justify-content 0.1s, transform 0.3s, opacity 0.2s ease-in 0.1s;
}



/* Experience section styles */
.resumeItemContainer {
    position: relative;
    width: 100%;
    height: auto;
}

/* Research and Projects section styles */
.projectContainer {
    margin-top: 60px;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;

}

/* Education section styles */
.educationContainer {
    display: flex;
    flex-flow: row;
    width: 100%;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin-top: 80px;
}